import { Injectable } from '@angular/core';
import { Router } from '@angular/router'

@Injectable({
  providedIn: 'root'
})
export class AuthService {
	constructor(
		private router: Router
		) {

	}
	canActivate(): boolean {
    if (!localStorage.getItem('ciid')) {
      this.router.navigate(["/"]);
      return false;
    }
    else
      return true;
  }
}
