import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { 
  FormControl, 
  FormGroup, 
  Validators } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-invoices',
  templateUrl: './invoices.component.html',
  styleUrls: ['./invoices.component.css']
})
export class InvoicesComponent {
  fact: any
  invoices: any[] = new Array<any>();
  fiid: string
  invoiceRef: string
  invoiceForm = new FormGroup ({
    ref: new FormControl('', Validators.required),
    cust_ref: new FormControl('', Validators.required),
    total_cost: new FormControl('', Validators.required)
  });

  constructor(
    private db: AngularFireDatabase,
    private router: Router
    ) {
    this.fact = JSON.parse(localStorage.getItem('ciid'))
    this.fiid = this.fact.ciid.toString()
    this.invoiceRef = `invoices/${this.fiid}`
    db.object(this.invoiceRef)
    .valueChanges()
    .subscribe((invoices) => {
      this.invoices = new Array<any>()
      if (!invoices) 
        return
      Object.keys(invoices).forEach((key) => {
        invoices[key].qrcode = `{"fiid":"${this.fiid}","key":"${key}","qr":"i002"}`
        this.invoices[invoices[key].ref] = invoices[key]
      })
      //this.invoices = invoices
    })
  }

  logout() {
    localStorage.removeItem('ciid')
    this.router.navigate(['/'])
  }

  update(key) {
    let invoice = this.invoices[key]
    this.invoiceForm.setControl("id", new FormControl(key))
    Object.keys(invoice).forEach((key) => {
      this.invoiceForm
      .setControl(key, new FormControl(invoice[key]))
    })
  }

  save() {
    let id
    let formValue = this.invoiceForm.value
    let invoiceRef$ = this.db.list(this.invoiceRef)
    let _invoiceRef$ = this.db.list(`_invoices/${formValue.ref}`)

    let key: string
    if (formValue.qrcode) {
      key = JSON.parse(formValue.qrcode).key
      delete formValue.qrcode
    }
    else
      key = this.db.createPushId()

    let invoice = this.invoices[formValue.id];
    if (invoice) {
      delete formValue.id
      this.db.list(`_invoices/${invoice.ref}`).remove()
    }

    let __Ref$ = invoiceRef$
    .update(key, formValue)
    __Ref$
    .then(() => {
      _invoiceRef$.update(this.fiid, { key: key })
      .then(() => {
        this.invoiceForm.reset()
      })
    })
  }

  onSubmit() {
    if (!this.invoiceForm.valid)
      return

    let formValue = this.invoiceForm.value
    let ref = formValue.ref
    /*this.db.database.ref(`_invoices`)
    .child(ref)
    .once('value').then((snapshot) => {
      if (!formValue.qrcode && snapshot.exists())
        return*/
    if (!this.invoices[ref])
      this.save()
    else
      alert('Cette référence existe déjà')
    //})

  }

  remove(key) {
    let invoice = this.invoices[key]
    let qrcode = JSON.parse(invoice.qrcode)
    this.db.list(`invoices/${qrcode.fiid}/${qrcode.key}`)
    .remove()
    .then(() => {
      this.db.list(`_invoices/${invoice.ref}`)
      .remove()
    })
  }
}
